// ** Router imports
import { lazy, useEffect, useState, Suspense } from "react"

// ** Router imports
import { useRoutes, Navigate } from "react-router-dom"

// ** Layouts
import BlankLayout from "@layouts/BlankLayout"

// ** Hooks Imports
import { useLayout } from "@hooks/useLayout"
// ** Utils
import { getUserData, getHomeRouteForLoggedInUser } from "../utility/Utils"

// ** GetRoutes
import { getRoutes } from "./routes"

import { GoogleOAuthProvider } from "@react-oauth/google"
import Spinner from "../@core/components/spinner/Fallback-spinner"
// ** Components
const Error = lazy(() => import("../views/pages/misc/Error"))
const Maintenance = lazy(() => import("../views/pages/misc/Maintenance"))
const Login = lazy(() => import("../views/pages/authentication/Login"))
const NotAuthorized = lazy(() => import("../views/pages/misc/NotAuthorized"))

const PublicPage = lazy(() => import("@src/shared/public_pages"))

import { store } from "../redux/store"

const Router = ({ loading }) => {
  // ** Hooks
  const { layout } = useLayout()
  const allRoutes = getRoutes(layout)

  const getHomeRoute = () => {
    const user = getUserData()
    if (user) {
      return getHomeRouteForLoggedInUser(user.role_name)
    } else {
      return "/login"
    }
  }

  const RedirectSite = () => {
    window.location.href = "/website/naxos/saba.html"
    return <></>
  }

  const routes = useRoutes([
    {
      path: "/",
      index: true,
      element: <Navigate replace to={getHomeRoute()} />
    },
    {
      path: "/login",
      element: <BlankLayout />,
      children: [
        {
          path: "/login",
          element: (
            <GoogleOAuthProvider clientId="1096092734184-n0tcl5abejf0s0r2t4fmoi3qf4fhen76.apps.googleusercontent.com">
              <Login />{" "}
            </GoogleOAuthProvider>
          )
        }
      ]
    },
    {
      path: "/error",
      element: <BlankLayout />,
      children: [
        { path: "/error", element: <Error /> },
        { path: "/error/403", element: <NotAuthorized /> }
      ]
    },
    {
      path: "/public",
      element: <RedirectSite />
    },
    // {
    //   path: "/public/:pageId",
    //   element: <PublicPage />,
    //   meta: {
    //     publicRoute: true,
    //     layout: "blank"
    //   }
    // },
    // {
    //   path: "/public/:moduleId/:pageId",
    //   element: <PublicPage />,
    //   meta: {
    //     publicRoute: true,
    //     layout: "blank"
    //   }
    // },
    {
      path: "*",
      element: <BlankLayout />,
      // children: [{ path: "*", element: <Error /> }]
      children: [{ path: "*", element: <Spinner /> }]
    },
    ...allRoutes
  ])

  const initBlank = useRoutes([
    {
      path: "/",
      index: true,
      element: <Navigate replace to={getHomeRoute()} />
    },
    {
      path: "/login",
      element: <BlankLayout />,
      children: [
        {
          path: "/login",
          element: (
            <GoogleOAuthProvider clientId="1096092734184-n0tcl5abejf0s0r2t4fmoi3qf4fhen76.apps.googleusercontent.com">
              <Login />{" "}
            </GoogleOAuthProvider>
          )
        }
      ]
    },
    {
      path: "/auth/not-auth",
      element: <BlankLayout />,
      children: [{ path: "/auth/not-auth", element: <NotAuthorized /> }]
    },
    {
      path: "/error",
      element: <BlankLayout />,
      children: [
        { path: "/error", element: <Maintenance /> },
        { path: "/error/403", element: <NotAuthorized /> }
      ]
    },
    {
      path: "/public",
      element: <RedirectSite />
    },
    // {
    //   path: "/public",
    //   element: <PublicPage />,
    //   meta: {
    //     publicRoute: true,
    //     layout: "blank"
    //   }
    // },
    // {
    //   path: "/public/:pageId",
    //   element: <PublicPage />,
    //   meta: {
    //     publicRoute: true,
    //     layout: "blank"
    //   }
    // },
    // {
    //   path: "/public/:moduleId/:pageId",
    //   element: <PublicPage />,
    //   meta: {
    //     publicRoute: true,
    //     layout: "blank"
    //   }
    // },
    {
      path: "*",
      element: <BlankLayout />,
      // children: [{ path: "*", element: <Error /> }]
      children: [{ path: "*", element: <Spinner /> }]
    },
    ...allRoutes
  ])

  if (store.getState()?.my_menu?.dataMyMenu?.length > 0) {
    return routes
  } else {
    return initBlank
  }
}

export default Router
