import React, { useEffect, Suspense, useRef, useState } from "react"
import "@nosferatu500/react-sortable-tree/style.css"
import { useNavigate } from "react-router-dom"
import Router from "./router/Router"
import Spinner from "./@core/components/spinner/Fallback-spinner"
import { store } from "./redux/store"
import { useDispatch, useSelector } from "react-redux"
import { getDomainInfo } from "@store/domainInfo"
import { getMyNotifications } from "@store/notifications"
import { getUserData, isUserLoggedIn } from "@utils"
import {
  replacePrimaryColor,
  replacePrimaryColorLate
} from "@src/utility/CssColorReplacer"
import { getAppMenu } from "@src/views/apps/menu/store/my_menu"
import { getUnits } from "@src/views/apps/unit/store/my_unit"
import Swal from "sweetalert2"
import { library } from "@fortawesome/fontawesome-svg-core"
import * as Icons from "@fortawesome/free-solid-svg-icons"
import { BUILD_DATE } from "@src/buildDate.js"
import useJwt from "@src/auth/jwt/useJwt"
import { setAppNeedUpdate } from "./redux/developer"
const axios = useJwt.ssoAxiosInterceptor

const iconList = Object.keys(Icons)
  .filter((key) => key !== "fas" && key !== "prefix")
  .map((icon) => Icons[icon])

library.add(...iconList)

const App = () => {
  const dispatch = useDispatch()
  const effectRan = useRef(false)
  const navigate = useNavigate()

  function getFaviconEl() {
    return document.getElementById("favicon")
  }

  async function pingServers() {
    // let serversList = JSON.parse(sessionStorage.getItem("serversList") ?? "[]")
    // await Promise.all(
    //   serversList?.map(async (url) => {
    //     try {
    //       const response = await fetch(url, { method: "HEAD" })
    //       // console.log(lo)
    //       return response.ok
    //     } catch (error) {
    //       console.error(error)
    //       // return false
    //     }
    //   })
    // )
  }

  useEffect(() => {
    if (
      isUserLoggedIn() !== null &&
      window.location.pathname !== "/error" &&
      window.location.pathname !== "/public"
    ) {
      const timeToFetch = 15 //in seconds
      let timer = null

      const handleVisibilityChange = () => {
        const datetime = new Date()
        if (document.hidden) {
          localStorage.setItem("inactive", Date.parse(datetime) / 1000)
          clearInterval(timer)
        } else {
          const lastTimeStr = localStorage.getItem("inactive")
          const timestamp = parseInt(lastTimeStr, 10)
          const lastTime = new Date(timestamp * 1000)
          const diffInSeconds =
            Math.abs(datetime.getTime() - lastTime.getTime()) / 1000
          if (diffInSeconds >= timeToFetch) {
            console.log(
              `Fetching data after ${diffInSeconds} seconds of inactivity`
            )
            store
              .dispatch(getMyNotifications({ unread_count: 1 }))
              .then((res) => {
                if (res?.payload) {
                  const count = res?.payload[0]?.count_unread ?? 0
                  if (count) {
                    const countFix =
                      count > 0 && count <= 20 ? `(${count})` : "(20+)"
                    document.title = `${countFix} ${document.title.replaceAll(
                      countFix,
                      ""
                    )}`
                    pingServers()
                  }
                }
              })
          } else {
            timer = setInterval(() => {
              const diffInSeconds =
                Math.abs(datetime.getTime() - lastTime.getTime()) / 1000
              if (diffInSeconds >= timeToFetch) {
                console.log(
                  `Fetching data after ${diffInSeconds} seconds of inactivity`
                )
                store
                  .dispatch(getMyNotifications({ unread_count: 1 }))
                  .then((res) => {
                    if (res?.payload) {
                      const count = res?.payload[0]?.count_unread ?? 0
                      if (count) {
                        const countFix =
                          count > 0 && count <= 20 ? `(${count})` : "(20+)"
                        document.title = `${countFix} ${document.title.replaceAll(
                          countFix,
                          ""
                        )}`
                      }
                      pingServers()
                    }
                  })
                clearInterval(timer)
              }
            }, 1000)
          }
        }
      }

      document.addEventListener("visibilitychange", handleVisibilityChange)

      return () => {
        clearInterval(timer)
        document.removeEventListener("visibilitychange", handleVisibilityChange)
      }
    }
  }, [])

  const syncVersion = async () => {
    const userData = getUserData()
    // console.log(userData)
    if (userData?.isDeveloper) {
      try {
        // console.log(BUILD_DATE)
        const dateString = BUILD_DATE
        const year = dateString.slice(0, 4)
        const month = dateString.slice(4, 6)
        const day = dateString.slice(6, 8)
        const hour = dateString.slice(9, 11)
        const minute = dateString.slice(11, 13)
        const dateCurrentStr = `${year}-${month}-${day}T${hour}:${minute}:00.000Z`

        const dateCurrent = new Date(dateCurrentStr)
        // console.log(dateCurrent)

        const getLastVerData = await axios({
          method: "post",
          url: `/fe_ver`
        })
        // console.log(getLastVerData)
        const getLastVer = getLastVerData?.data?.data
        if (getLastVer) {
          const dateLastString = getLastVer?.date
          if (dateLastString) {
            const dateLastWithoutSeconds = dateLastString?.replace(
              /:\d{2}Z$/,
              ":00Z"
            )
            const dateLast = new Date(dateLastWithoutSeconds)
            // eslint-disable-next-line no-mixed-operators
            const dateLastFix = new Date(
              // eslint-disable-next-line no-mixed-operators
              dateLast.getTime() + 7 * 60 * 60 * 1000
            )
            // console.log(
            //   dateLast,
            //   dateLastFix,
            //   dateLastString,
            //   dateLastWithoutSeconds
            // )
            const dateLastFixStr = dateLastFix.toISOString()
            const dateLastFixStrDisplay = `${dateLastFixStr
              .slice(0, 10)
              .replace(/-/g, "")}-${dateLastFixStr
              .slice(11, 16)
              .replace(":", "")}`
            // console.log(dateLastFixStrDisplay)
            const dateLastFix2 = new Date(dateLastFixStr)
            // eslint-disable-next-line no-mixed-operators
            // console.log(dateCurrentStr, dateLastFixStr, dateLastWithoutSeconds)
            // console.log(dateCurrent, dateLastFix2)
            // const isNeedUpdate = dateCurrent < dateLastFix2
            const isNeedUpdates = () => {
              const diff = (dateLastFix2 - dateCurrent) / 1000 / 60
              // console.log(diff)
              return diff > 10
            }
            const isNeedUpdate = isNeedUpdates()
            // console.log(isNeedUpdate)
            if (isNeedUpdate) {
              dispatch(
                setAppNeedUpdate({
                  state: isNeedUpdate,
                  lastVer: dateLastFixStrDisplay,
                  currentVer: dateString
                })
              )
              // .then((response) =>
              //   console.log(response)
              // )
            }
          }
        }
      } catch (error) {
        console.error(error)
      }
    }
  }

  const timeouts = useRef([])

  useEffect(() => {
    if (!effectRan.current) {
      if (navigator.onLine) {
        if (window.location.pathname !== "/public") {
          dispatch(getDomainInfo({})).then((res) => {
            if (res?.meta?.requestStatus === "rejected") {
              // console.error(res)
              console.error("Not Registered")
              Swal.fire({
                icon: "error",
                title: "Connection Failed!",
                text: "There is a problem connecting to the server or this app is not yet registered with Saba Official.",
                confirmButtonText: "Refresh",
                customClass: {
                  confirmButton: "btn btn-primary"
                }
              }).then((result) => {
                if (result.isConfirmed) {
                  location.reload()
                }
              })
            } else {
              const logoResp = res.payload?.app?.logo
              if (
                logoResp?.favicon ||
                res.payload?.unit?.unit_app_attributes?.favicon
              ) {
                const favicon = getFaviconEl()
                favicon.href =
                  res.payload?.unit?.unit_app_attributes?.favicon ??
                  logoResp.favicon
              }

              const primaryColorBe =
                res.payload?.unit?.unit_app_attributes?.layout?.primaryColor ??
                res.payload?.app?.setting?.primaryColor
              if (primaryColorBe) {
                replacePrimaryColor(primaryColorBe)
                const timeout1 = setTimeout(() => {
                  replacePrimaryColorLate(primaryColorBe)
                }, 500)
                timeouts.current.push(timeout1)
                // setTimeout(() => {
                //   replacePrimaryColorLate(primaryColorBe)
                // }, 500)
              }
              if (window.location.pathname !== "/error") {
                if (isUserLoggedIn() !== null) {
                  store.dispatch(getAppMenu({})).then((res) => {
                    if (res?.meta?.requestStatus !== "rejected") {
                      store.dispatch(getUnits({}))
                      const timeout2 = setTimeout(() => {
                        store
                          .dispatch(getMyNotifications({ unread_count: 1 }))
                          .then((res) => {
                            if (res?.payload) {
                              const count = res?.payload[0]?.count_unread ?? 0
                              if (count) {
                                const countFix =
                                  count > 0 && count <= 20
                                    ? `(${count})`
                                    : "(20+)"
                                document.title = `${countFix} ${document.title.replaceAll(
                                  countFix,
                                  ""
                                )}`
                              }
                            }
                          })
                      }, 2000)
                      timeouts.current.push(timeout2)
                      const timeout3 = setTimeout(() => {
                        syncVersion()
                      }, 45000)
                      timeouts.current.push(timeout3)
                      // setTimeout(() => {
                      //   syncVersion()
                      // }, 15000)
                    } else {
                      localStorage.setItem("lastUrl", window.location.href)
                      navigate("/error")
                    }
                  })
                } else {
                  if (
                    (window.location.pathname !== "/login" ||
                      window.location.pathname?.indexOf("/public") !== -1) &&
                    window.location.pathname !== "/reset-password" &&
                    window.location.pathname !== "/forgot-password" &&
                    window.location.pathname !== "/register"
                  ) {
                    navigate("/login")
                  } else {
                    if (window.location.pathname === "/reset-password") {
                      navigate(
                        window.location.pathname + window.location.search
                      )
                    } else if (
                      window.location.pathname === "/forgot-password"
                    ) {
                      navigate("/forgot-password")
                    } else if (
                      window.location.pathname === "/register" &&
                      (res.payload?.app?.setting?.enable_registration ||
                        res.payload?.unit?.unit_app_attributes
                          ?.enable_registration)
                    ) {
                      navigate("/register")
                    } else {
                      navigate("/login")
                    }
                  }
                }
              }
            }
          })
        }
      } else {
        console.error("Offline")
        Swal.fire({
          icon: "error",
          title: "You are not connected to internet",
          text: "Please check your connection to the internet.",
          confirmButtonText: "Refresh",
          customClass: {
            confirmButton: "btn btn-primary"
          }
        }).then((result) => {
          if (result.isConfirmed) {
            location.reload()
          }
        })
      }
    }

    return () => {
      effectRan.current = true
      // Clear all timeouts when the component unmounts or effect is cleaned up
      timeouts.current.forEach((timeout) => clearTimeout(timeout))
      timeouts.current = []
    }
  }, [dispatch])

  return (
    <Suspense fallback={<Spinner />}>
      <Router />
    </Suspense>
  )
}

export default App
