export const replacePrimaryColorLate = (primaryColorBe) => {
  console.log("replace head style late begin...")
  const rgbColorVal = hexToRgbVal(primaryColorBe)
  const styleElement = document.createElement("style")
  styleElement.textContent = `
      .fc .fc-toolbar .fc-button-group .fc-button-primary:not(.fc-prev-button):not(.fc-next-button).fc-button-active, .fc .fc-toolbar .fc-button-group .fc-button-primary:not(.fc-prev-button):not(.fc-next-button):hover {
        background-color: rgba(${rgbColorVal}, 0.2) !important;
        border-color: ${primaryColorBe} !important;
        color: ${primaryColorBe} !important;
      }
    `
  document.head.appendChild(styleElement)

  console.log("replace head style late end...")
}
